<template>
  <div :style='{height: ht - 50 + "px"}' style='overflow-y: auto;'>
    <div style='background-image: linear-gradient(#2771FF, #0039EE); width: 100%; height: auto; overflow: hidden;'>
      <van-search
              background='none'
              v-model="search"
              placeholder="输入你感兴趣的内容"
              @search="onSearch"
      />
      <van-swipe :autoplay="3000">
        <van-swipe-item v-for="(image, index) in images" :key="index">
          <img :src="image" style='width: 92%; height: 260px; margin-bottom: -20px; border-radius: 15px;'/>
        </van-swipe-item>
      </van-swipe>
    </div>
    <div style='padding: 0px 15px; text-align: left; box-sizing: border-box; line-height: 46px; background: #fff; height: 46px; width: 100%;'>
      <span style="display: inline-block;width: 80%;overflow: hidden;white-space: nowrap;text-overflow:ellipsis;">安全经验分享 — 一名死里逃生的员工记录</span>
      <span @click='$router.push("./gonggao")' style='float: right; color: rgba(153, 153, 153, 1);'>更多>></span>
    </div>
    <div style='padding: 0px 15px; background: none;'>
      <div v-for='(item, index) in notice' :key='index'
           style=' text-align: left; padding-left: 50px; background: rgba(0, 87, 255, 0.1); padding: 3px 0px; border-radius: 20px; height: 32px; line-height: 32px; margin-top: 10px;'>
        <div style='border-radius: 50%; text-align: center; margin-top: -2px; width: 35px; height: 35px; background: rgba(0, 87, 255, 0.2); float: left'>
          <img src="../assets/gonggao@2x.png" width='20px;' style='margin-top: 8px;;'/>
        </div>
        {{item.title}}
      </div>
    </div>

    <div style='padding: 10px 15px;'>
      <div style='color:rgba(51, 51, 51, 1); text-align: left; font-size: 26px; font-weight: bold;'>课程中心</div>
      <div v-for='(item, index) in kemus' :key='index'
           style='background: #fff;border-radius: 8px;padding: 15px; margin-top: 10px; text-align: left; height: 75px; position: relative;' @click="goWorkList(item.id)">
        <img src="../assets/jiantou@2x.png" style='position: absolute; right: 18px; top: 42px; width: 8px;'/>
        <div style='font-size: 18px; color:rgba(51, 51, 51, 1); font-weight: bold;'>{{item.name}}</div>
        <div style='margin-top: 15px; width: 50%; float:left;'>
          <img src="../assets/tiliang@2x.png" style='width: 31px; vertical-align: middle; '/>
          题量 {{item.studys_count}}/{{item.kemus_count}}
        </div>
        <div style='margin-top: 15px; width: 50%; float:left;'>
          <img :src="item.user_studys===0?EndNoImg:EndImg" style='width: 31px; vertical-align: middle; '/>
          状态 {{item.user_studys===0?'未完成':'已完成'}}
        </div>
      </div>
    </div>
    <div style='height: 50px; width: 100%;'></div>
    <Footer></Footer>
  </div>
</template>

<script>
  import Footer from '@/components/footer/footer'
  import EndNoImg from '@/assets/end_no.png'
  import EndImg from '@/assets/end.png'

  export default {
    name: "home",
    data() {
      return {
        ht: window.screen.height,
        search: '',
        images: [
          'https://img.yzcdn.cn/vant/apple-1.jpg',
          'https://img.yzcdn.cn/vant/apple-2.jpg'
        ],
        kemus: [],
        notice: [],
        EndNoImg: EndNoImg,
        EndImg: EndImg
      }
    },
    components: {
      Footer
    },
    mounted() {
      this.getIndex()
    },
    methods: {
      async getIndex(){
        const {err,data} = await this.$service.index.home();
        if(err){
          this.$toast(err.message);
          return;
        }
        if(data.code === 400) {
          this.$toast('请前往登录')
          this.$router.push('/login');
          return;
        }
        if(data.code === 300) {
          this.$toast('请先完善资料')
          this.$router.push('/wanshan');
          return;
        }
        this.kemus = data.kemus;
        this.notice =data.notice;
      },
      goWorkList(id){
        this.$router.push({
          name: 'kechengList',
          params: {
            id: id
          }
        })
      },
      onSearch() {
      }
    }
  }
</script>

<style scoped>

</style>